.no-data-container {
  margin-top: 60px;
  display: flex;
  justify-content: center;
  align-items: center;

  @media screen and (max-width: 400px) {
    margin-top: 0px;
  }

  p {
    width: 300px;
    position: absolute;
    color: rgb(249, 249, 249);
    font-family: "Inconsolata", serif;
    padding: 10px;
    margin-top: -250px;

    @media screen and (max-width: 400px) {
      width: 200px;
      margin-top: -120px;
      font-size: 12px;
    }
  }

  img {
    @media screen and (max-width: 400px) {
      width: 250px;
      margin-top: 50px;
    }
  }
}

.less-than-5-albums-container {
  margin-bottom: 30px;
  margin-top: 45px;
  margin: 65px 10px 40px;
  padding: 20px;
  border-radius: 10px;
  border: 2px solid $color-navyblue;
  background-color: $color-greyblue;

  h2 {
    max-width: 300px;
    color: rgb(19, 11, 81);
    font-family: "Expletus Sans", serif;
    font-size: 19px;
    text-align: center;

    @media screen and (max-width: 688px) {
      font-size: 16px;
    }
    @media screen and (max-width: 320px) {
      font-size: 14px;
    }
  }
}

.artist-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(19, 11, 81);
  border: 2px solid $color-greyblue;
  margin: 40px;
  border-radius: 20px;
  position: relative;
  justify-content: flex-start;

  @media screen and (max-width: 572px) {
    margin: 10px;
  }


  #artist-legend {
    font-family: "expletus Sans", serif;
    font-weight: bold;
    font-size: 35px;
    padding: 0 15px;
    z-index: 0;
    white-space: wrap;
    overflow: hidden;
    text-align: center;
    max-width: 400px;


    @media screen and (max-width: 450px) {
      font-size: 23px;
      
    }

  }


  img {
    border-radius: 10px;
    box-shadow: 2px 2px 5px rgb(96, 137, 183);
  }

button {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: $color-skyblue;
  border-radius: 10px;
  border-width: 1px;
  border-color: $color-navyblue;
  color: #130b51;
  border-style: solid;
  margin: 1px;
  cursor: pointer;
  font-family: "Poppins", serif;
  width: 90%;
  height: 40px;
  font-size: 15px;

  @media screen and (max-width: 572px) {
    width: 90%;
    }
  }

}

.quiz-inner-container {
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  margin-top: 40px;
  margin-bottom: 30px;
  padding: 12px 20px 40px 20px;
  border-radius: 10px;
  border: 2px solid $color-navyblue;;
  background-color: $color-greyblue;

  @media screen and (max-width: 450px) {
    padding: 0px 10px 30px 10px;
  }

  
}

//INFO ON HOVER & ON CLICK IN QUIZ CONTAINER INACTIVE
.show-info {
  z-index: 0;
  opacity: 0;
  visibility: hidden; 
  transition: opacity 0.3s ease, visibility 0s 0.3s;
  position: absolute; 
  top: 83px;
  font-size: 15px;
  background-color: rgba(214, 227, 241, 0.95);
  border-radius: 10px;
  box-shadow: 2px 2px 5px rgb(96, 137, 183);
  padding: 5px;
  width: 200px;

}

#info-icon {
  cursor: pointer;
  padding: 5px;
  z-index: 1;

  @media screen and (max-width: 450px) {
    padding: 10px 5px
  }
}

#info-icon:hover + .show-info {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease, visibility 0s 0s;
}


.quiz-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 600px;
  text-align: center;
  border-radius: 15px;
  transition: .4s ease-in-out;
  font-family: "Inconsolata", serif;

  @media screen and (max-width: 450px) {
    width: 95%;
  }

  .score-h2 {
    padding: 25px 25px 25px;
  }

  h2 {
    padding: 0px 0 25px;
    font-size: 18px;
    max-width: 400px;

    @media screen and (max-width: 450px) {
      font-size: 15px;
    }
  }

  &.is-active {
    background-color: rgba(110, 149, 195, 0.328);
    height: 600px;
    margin: 45px 12.5px 20px;
    box-shadow: 2px 2px 5px rgb(96, 137, 183);
    padding: 27px;
    width: 75%;
  
    @media screen and (max-width: 850px) {
      width: 75%;
    }
    @media screen and (max-width: 685px) {
      height: 650px;
    }
  
    h2 {
      padding: 0 0 20px;
      font-size: 20px;
      @media screen and (max-width: 500px) {
        font-size: 17px;
      }
    }
    h3 {
      padding: 20px 0;
      font-size: 20px;

      @media screen and (max-width: 500px) {
        font-size: 17px;
      }
    }
  }
}

.selected-album-image-box {
  height: 250px;

  @media screen and (max-width: 572px) {
    width: 190px;
    height: 190px;
  }
  @media screen and (max-width: 320px) {
    width: 170px;
    height: 170px;
  }
  img {
    width: 250px;
    height: 250px;

    @media screen and (max-width: 572px) {
      width: 190px;
      height: 190px;
    }
    
    @media screen and (max-width: 320px) {
      width: 170px;
      height: 170px;
    }
  }
}

.answer-images-box {
  height: 220px;
  display: flex;
  align-items: center;
  justify-content: center;

  @media screen and (max-width: 685px) {
    height: 400px;
    flex-direction: column
  }

  img {
    margin: 2px;
    cursor: pointer;
    height: 140px;
    width: 140px;
    border: 3px solid transparent;
    box-shadow: none;

    @media screen and (max-width: 685px) {
      width: 120px;
      height: 120px;
    }
    @media screen and (max-width: 320px) {
    }
  }
}

.answer-image-is-correct {
  border: 3px solid rgb(7, 177, 7) !important;
  animation: bounce 1s ease-in-out; 
}

.answer-image-is-incorrect {
  opacity: 0.5;
}

.question-box {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;

  h3 {
  text-align: center;
  margin: 12px;

    @media screen and (max-width: 685px) {
      margin-top: 0px;
  }

  }
  p {
  margin: 5px;

    @media screen and (max-width: 685px) {
      margin-top: 20px;
    }
  }
}

#track-question-h3 {
  margin-bottom: 15px
}

.answers-button-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
  align-content: center;

  .answer-button-is-correct {
    opacity: 1;
    background-color: rgb(72, 162, 45);
    color: white;
    animation: 1s bounce ease-in-out;
    animation-timing-function: linear;
  }
}

.answers-button-box > *:last-child {
  margin-bottom: 20px;
}

@keyframes bounce {
    0% {
      transform: scale(1);  
    }
    20% {
      transform: scale(1.2);  
    }
    50% {
      transform: scale(1);  
    }
    75% {
      transform: scale(1.1);  
    }
    100% {
      transform: scale(1); 
    }
  }

.answer-button-is-incorrect {
  opacity: 0.5;
}

#start-quiz-button {
  
  @media screen and (max-width: 500px) {
    font-size: 14px;
    }
}

#next-button {
width: 70%;
margin-bottom: 10px;

  @media screen and (max-width: 850px) {
  width: 90%;
  }
  @media screen and (max-width: 500px) {
  width: 100%;
  font-size: 14px;
  }
}

#year-buttons {
  width: 70%;

  @media screen and (max-width: 850px) {
    width: 90%;
  }
  @media screen and (max-width: 500px) {
    width: 100%;
    font-size: 14px;
  }
}

#album-title-buttons {
  font-size: 13.5px;
  width: 70%;

  @media screen and (max-width: 850px) {
    width: 90%;
  }
  @media screen and (max-width: 500px) {
    width: 100%;
    font-size: 12px;
  }
}

/* ALBUMS_LIST COMPONENT */
.albums-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 90%;
  padding: 10px 0 50px;

  img {
    margin: 3px;
    cursor: pointer;
    width: 150px;
    height: 150px;

    @media screen and (max-width: 572px) {
      width: 140px;
      height: 140px;
    }
    @media screen and (max-width: 320px) {
      width: 120px;
      height: 120px;
    }
  }
}

.album-cover-isdisabled{
  pointer-events: none;
  cursor: default;
}

/* ALBUM DETAILS WHEN USER CLICKS */

#tfi-close {
  padding-left: 5px;
  padding-top: 15px;
}

.album-show-container {
  background-color: rgb(89, 107, 139);
  box-shadow: 2px 2px 5px rgb(32, 34, 37);
  color: rgb(255, 255, 255);
  max-width: 800px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  opacity: 0;
  transition: 1s ease;
  z-index: 0;
  border-radius: 10px;
  padding: 0 10px 30px;

  &.is-active { 
    align-content: center;
    width: 90%;
    justify-content: center;
    opacity: 1;
    padding-bottom: 15px;
    margin: 10px 20px;

    img {
      object-fit: cover;
      height: 350px;
      width: 350px;
      box-shadow: 2px 2px 5px rgb(32, 34, 37);
      margin: 10px; 

      @media screen and (max-width: 828px) {
        width: 300px;
        height: 300px;
      }
      @media screen and (max-width: 480px) {
        width: 200px;
        height: 200px;
      }
    }
  }
}

.audio-loader {

  @media screen and (min-width: 940px) {
    position: absolute;
    left:47%;
    top: 30%;
  }
  @media screen and (max-width: 940px) {
    padding-top: 25%;
    padding-bottom: 25%;
    position: relative;
  }

}


